import React from "react";
import { getCurrencyFormat } from "../lib/utils";

// components
import CategoryBadges from "./CategoryBadges";

interface ProductCardProps {
  product: Product;
  categories: ProductCategory[];
  addToCart: AddToCart;
}

const ProductCard: React.FC<ProductCardProps> = ({
  product,
  categories,
  addToCart
}) => {
  return (
    <div className="d-flex">
      <div className="card m-3 bg-dark shadow-sm">
        <div className="overlay">
          <img
            className="card-img-top img-fluid"
            alt={product.name}
            src={product.imageUrl}
          />
          <div className="card-description">
            <h4>{product.name}</h4>
            <p>{product.description}</p>
          </div>
        </div>
        <div className="card-footer">
          <div className="d-flex mb-3">
            <CategoryBadges product={product} categories={categories} />
          </div>
          <div className="d-flex flex-md-wrap align-items-center">
            <div className="h5 font-weight-bold mb-0 mr-auto">
              {getCurrencyFormat(product.price)}
            </div>
            <button
              className="btn btn-primary"
              onClick={() => addToCart(product, 1)}
            >
              Add to cart
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductCard;
